.form-contacts {
  position: relative;
  min-height: 337px;
  margin: 0 auto;
  background: linear-gradient(90deg, rgba(16, 38, 84, 1) 50%, rgba(255, 255, 255, 1) 50%);

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    width: 100%;
    height: 1px;
    background-color: #74819c;
  }
}

.form-contacts__wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
  max-width: 1366px;
  min-width: 1200px;
  margin: 0 auto;
  padding: 0;
}
